.blog-wrap{position:absolute;top:60px;bottom:60px;left:60px;right:60px;border:4px solid #1eedff;border-radius:8px;background:rgba(0,0,0,.5)}.blog-title{padding:.5em 0;letter-spacing:3px;text-align:center;color:#eee;font-weight:normal;font-family:"华文行楷","楷体","Microsoft Yahei","宋体",Arial,sans-serif;border-bottom:1px solid #1eedff}.blog-category-list{display:flex;padding:20px;flex-wrap:wrap}.blog-category-list li{padding:20px;min-width:100px;list-style:none;text-align:center;cursor:pointer;border-radius:4px;border:1px solid rgba(0,0,0,0)}.blog-category-list li:hover{border:1px solid currentColor}.blog-category-list p{font-size:24px}.blog-category-list .blog-icon{display:block;text-align:center;font-size:48px;pointer-events:none}.blog-category-list .blog-icon svg{width:1em;height:1em}.blog-category-list .blog-icon path{fill:currentColor}.category-algorithm{color:#00a2d4}.category-canvas{color:#4eb05d}.category-animate{color:#4dd7c7}.category-base{color:#ddd}.category-comp{color:#4ea9f3}.category-css{color:#be5cf3}.category-js{color:#f64444}.category-game{color:#ecae18}.category-math{color:#02d8f3}.category-text{color:#d35417}.category-util{color:#0be879}